.infotip {
  display: inline;
  position: relative;

  button {
    background-color: transparent;
    border: none;
    display: inline-block;
    font-size: 24px !important;
    line-height: 24px !important;
    margin: 0 !important;
    padding: 0 !important;

    // used as with .button, but needed in case no forceDefaultElements is used
    box-sizing: border-box;
    cursor: pointer;
    text-align: center;
    text-decoration: none;
    vertical-align: middle;
    white-space: nowrap;

    &:focus {
      outline: none;
    }

    .iconUxt {
      color: rgba(var(--color-info), 1);
      line-height: inherit;
      margin: 0;
      vertical-align: baseline;

      @include iconUxt-regular(question);
    }
  }

  [role='tooltip'] {
    background-color: rgba(var(--color-interface-highlight), 1);
    border: 1px solid rgba(var(--color-border), 1);
    border-radius: 3px;
    box-sizing: border-box;
    font-size: 12px;
    left: 34px;
    line-height: 15px;
    max-width: 400px;
    min-width: 240px;
    opacity: 0;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    transition: opacity 0.2s ease-in-out;
    visibility: hidden;

    .infotip__wrapper {
      box-sizing: border-box;
      padding: 12px 16px;
      position: relative;
      z-index: 10;

      > :first-child {
        margin-top: 0;
      }

      > :last-child {
        margin-bottom: 0;
      }

      // shadow layer
      &:before {
        bottom: 0;
        box-shadow: 0 4px 12px 0 rgba(var(--color-shadow), 0.25);
        content: '';
        display: block;
        left: 0px;
        position: absolute;
        right: 0px;
        top: 0px;
        z-index: -1;
      }

      // compare with .dialog__close -> should be identical
      .infotip__close {
        box-sizing: border-box;
        display: inline-block;
        font-size: 24px;
        height: 24px;
        position: absolute;
        top: 8px;
        right: 4px;
        width: 24px;

        @include iconUxt-regular(cancel);
        text-decoration: none;
        transition: color 0.2s ease-in-out;

        &, &:visited {
          color: rgba(var(--color-info-dark), 1);
        }

        &:hover {
          color: rgba(var(--color-primary-light), 1) !important;
          cursor: pointer;
          text-decoration: none;
        }
      }

      .infotip__header {
        font-weight: bold;
        font-size: 14px;
        margin: 2px 0 4px 0;
      }

      img, figure {
        height: auto;
        margin: 8px 0;
        max-width: 100%;
      }
    }

    // infotip arrow "border"
    &:before {
      border-width: 8px;
      border-style: solid;
      border-color: transparent rgba(var(--color-border), 1) transparent transparent;
      content: "";
      left: -16px;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      z-index: 11;
    }

    // infotip arrow
    &:after {
      border-width: 8px;
      border-style: solid;
      border-color: transparent rgba(var(--color-interface-highlight), 1) transparent transparent;
      content: "";
      left: -15px;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      z-index: 12;
    }
  }

  // automatic hide, does not work on OSX with Firefox and Safari: https://developer.mozilla.org/en-US/docs/Web/HTML/Element/button#clicking_and_focus
  &.infotip--autohide {
    button:focus + [role='tooltip'] {
      opacity: 1;
      visibility: visible;
      z-index: 10;
    }

    // hover is needed on IOS, buttons don't work with focus in Safari
    // target Safari Browsers ONLY / questions/16348489/is-there-a-css-hack-for-safari-only-not-chrome
    @supports (-webkit-hyphens:none) {
      button:hover + [role='tooltip'] {
        opacity: 1;
        visibility: visible;
        z-index: 10;
      }
    }
  }

  &.is-shown {
    [role='tooltip'] {
      opacity: 1;
      visibility: visible;
      z-index: 10;
    }
  }

  // since clicking anywhere besides the initial button will remove its focus, making every possible link within the infotip un-clickable, we have to work around:
  button + [role='tooltip'] a:hover {
    position: relative;
    opacity: 1;
    visibility: visible;
    z-index: 10;
  }


  &.infotip--wide {
    [role='tooltip'] {
      min-width: 400px;
    }
  }

  &.infotip--left {
    [role='tooltip'] {
      left: auto;
      right: 34px;

      &:before {
        border-color: transparent transparent transparent rgba(var(--color-border), 1);
        left: auto;
        right: -16px;
      }

      &:after {
        border-color: transparent transparent transparent rgba(var(--color-interface-highlight), 1);
        left: auto;
        right: -15px;
      }
    }
  }

  &.infotip--top {
    [role='tooltip'] {
      bottom: 34px;
      left: 42%;
      top: auto;
      transform: translateX(-50%);

      &:before {
        border-color: rgba(var(--color-border), 1) transparent transparent transparent;
        bottom: -16px;
        left: 50%;
        right: auto;
        top: auto;
        transform: translateX(-50%);
      }

      &:after {
        border-color: rgba(var(--color-interface-highlight), 1) transparent transparent transparent;
        bottom: -15px;
        left: 50%;
        right: auto;
        top: auto;
        transform: translateX(-50%);
      }
    }
  }

  &.infotip--bottom {
    [role='tooltip'] {
      left: 42%;
      top: 34px;
      transform: translateX(-50%);

      &:before {
        border-color: transparent transparent rgba(var(--color-border), 1) transparent;
        left: 50%;
        right: auto;
        top: -16px;
        transform: translateX(-50%);
      }

      &:after {
        border-color: transparent transparent rgba(var(--color-interface-highlight), 1) transparent;
        left: 50%;
        right: auto;
        top: -15px;
        transform: translateX(-50%);
      }
    }
  }
}



