.keyValueWrapper {
	align-items: center;
	display: flex;
	justify-content: flex-start;
	margin-bottom: 12px;
	margin-bottom: rem(12px);

	&__icon { // minimum icon size
		font-size: 24px;
		font-size: rem(24px);
		margin-right: 16px;
		margin-right: rem(16px);
	}

	&__keyValue {
		align-items: flex-start;
		display: flex;

		.keyValue {
			&__key,
			&__value {
				color: rgba(var(--color-font-secondary), 1);
				font-family: $defaultFont;
				font-size: 14px;
				font-size: rem(14px);
				line-height: 20px;
				line-height: rem(20px);
			}

			&__value {
				color: rgba(var(--color-font), 1);
				margin-left: 16px;
				margin-left: rem(16px);

				&--black,
				&--bolder {
					font-family: $defaultFont;
					font-weight: 900;
				}

				&--bold {
					font-family: $defaultFont;
					font-weight: bold;
				}

				> * {
					margin-top: 0;
				}

				> .breadcrumb {
					padding-top: 0 !important;
				}
			}

			&__unit {
				color: inherit;
				margin-left: 0.3em; // browsers dont end an inline element at the very right , hence 8 pixels are too wide.
			}
		}

		@include breakpoint($xs) {
			align-items: flex-start;
			flex-direction: column;

			.keyValue__value {
				margin-left: 0;
			}
		}

		&--column {
			align-items: flex-start;
			flex-direction: column;

			.keyValue__value {
				margin-left: 0;
			}
		}

		&--reverse {
			align-items: flex-start;
			flex-direction: column-reverse;

			.keyValue__value {
				margin-left: 0;
			}
		}
	}

	&--large {
		.keyValueWrapper__icon {
			font-size: 44px;
			font-size: rem(44px);
			line-height: 50px;
			line-height: rem(50px);
		}

		.keyValue {
			&__key {
				font-size: 16px;
				font-size: rem(16px);
				line-height: 22px;
				line-height: rem(22px);
			}

			&__unit {
				font-size: 22px;
				font-size: rem(22px);
				line-height: 28px;
				line-height: rem(28px);
			}

			&__value {
				font-size: 44px;
				font-size: rem(44px);
				line-height: 50px;
				line-height: rem(50px);
			}
		}
	}

	&--medium {
		.keyValueWrapper__icon {
			font-size: 24px;
			font-size: rem(24px);
			line-height: 24px;
			line-height: rem(24px);
		}

		.keyValue {
			&__key {
				font-size: 14px;
				font-size: rem(14px);
				line-height: 20px;
				line-height: rem(20px);
			}

			&__unit,
			&__value {
				font-size: 18px;
				font-size: rem(18px);
				line-height: 24px;
				line-height: rem(24px);
			}
		}
	}

	&.has-alignment {
		&-center {
			justify-content: center;

			.keyValueWrapper__keyValue {
				align-items: center;

				&--column .keyValue__key,
				&--reverse .keyValue__key {
					margin-right: 0;
				}
			}
		}

		&-right {
			justify-content: flex-end;
		}
	}
}

// for a container with more than one keyValueWrapper
.container .keyValueWrapper:not(:only-child),
.keyValueList {
	.keyValueWrapper__keyValue {
		flex-basis: 100%;

		.keyValue__key {
			flex-basis: 30%;
		}

		.keyValue__value {
			flex-basis: 70%;
		}

		@include breakpoint($xs) {
			.keyValue__key,
			.keyValue__value {
				flex-basis: auto;
			}
		}
	}

	// IE11 bugfix for flex-direction: column/column-reverse
	.keyValueWrapper__keyValue--column,
	.keyValueWrapper__keyValue--reverse {
		.keyValue__key,
		.keyValue__value {
			flex-basis: auto;
		}
	}

	// for alignment the flex basis has to be overwritten
	.has-alignment-right,
	.has-alignment-center {
		.keyValueWrapper__keyValue {
			flex-basis: auto;
		}
	}
}
