@mixin item__link {
    background-color: inherit;
    color: rgba(var(--color-font), 1);
    display: flex;
    text-decoration: none;
    @include hoverTransition($animate: (background-color, color));

    &:focus {
        background-color: rgba(var(--color-base800), 0.5);

        box-shadow: inset 0 0 0 2px rgba(var(--color-primary-light), 1);
        // IE11 does not support outline-offset, hence the feature query
        outline: 0;
        @supports (outline-offset: -2px) {
            box-shadow: none;
            outline-offset: -2px;
            outline: 2px solid rgba(var(--color-primary-light), 1);
        }
    }

    &:hover {
        background-color: rgba(var(--color-base800), 0.5);

        .label {
            text-decoration: underline;
        }

        .description {
            text-decoration: none;
        }
    }

    &:active,
    &.is-activated {
        background-color: rgba(var(--color-base750), 0.7);
        
        // no focus outline on active
        box-shadow: none;
        outline: 0;
    }

    &.is-current {
        background-color: rgba(var(--color-primary-lightest), 0.5);
    }

    &.is-disabled {
        opacity: 0.5;
        pointer-events: none;
        &:hover {
            cursor: not-allowed;
            background-color: transparent;
            text-decoration: none;

            .label {
                text-decoration: none;
            }
        }

        &:focus {
            background-color: transparent;
            box-shadow: none;
            // IE11 does not support outline-offset, hence the feature query
            outline: 0;
        }
    }
}

@mixin item__action {
    align-items: center;

    box-sizing: border-box;
    color: rgba(var(--color-font), 1);
    display: flex;
    height: 48px;
    height: rem(48px);
    position: absolute;
    text-align: center;
    text-decoration: none;
    top: calc(50% - 24px);
    width: 52px;
    width: rem(52px);
    @include hoverTransition($animate: (background-color));

    &:after {
        background-color: rgba(var(--color-base900), 1);
        border: 1px solid rgba(var(--color-base200), 1);
        border-radius: 50%;
        box-sizing: border-box;
        display: inline-block;
        content: '';
        height: 32px;
        height: rem(32px);
        width: 32px;
        width: rem(32px);
        position: absolute;
        right: 12px;
        top: calc(50% - 16px);
        top: calc(50% - 1rem);
        z-index: 0;
    }

    &:hover:after {
        background-color: rgba(var(--color-base800), 0.5);
    }

    &:focus {
        outline: none;
    }

    &:focus:after {
        box-shadow: rgba(var(--color-primary-light), 1) 0 0 0 3px; // fake border color since we cannot use the outline here, button is not rectangular
        outline: none;
    }

    &:active:after,
    &.is-activated:after {
        background-color: rgba(var(--color-base750), 0.7);

        box-shadow: none;
    }
}


.listNavigation {
    background-color: rgba(var(--color-interface-secondary), 1);
    display: flex;
    flex-direction: column;
    height: 100%;

    // container for multiple lists
    .listNavigation__lists {
        background-color: inherit;
        flex-grow: 1;
        flex-basis: auto;
        overflow-y: auto;
        overflow-x: hidden;

    }

    // one single list header
    .listNavigation__header {
        background-color: inherit;
        border-bottom: 1px solid rgba(var(--color-border), 1);
        border-top: 1px solid rgba(var(--color-border), 1);
        color: rgba(var(--color-font), 1);
        flex-basis: auto;
        flex-shrink: 0;
        font-family: $defaultFont;
        font-size: 14px;
        font-size: rem(14px);
        font-weight: bold;
        line-height: 19px;
        line-height: rem(19px);
        padding-top: 14px;
        padding-bottom: 14px;
        padding-left: 16px;
        padding-right: 16px;

        &:first-child {
            border-top: 0;
        }

        &.is-advanced {
            background-color: inherit;
            padding: 0;
            position: relative;

            a.header__link {
                @include item__link();
                padding: 14px 16px 14px 54px;

                &:hover {
                    text-decoration: underline;
                }
            }

            a.header__back {
                @include item__action();
                @include iconUxt-regular(arrowSingleLeft);
                left: 0px;

                &:after {
                    left: 12px;
                }

                &:before {
                    font-size: 24px;
                    left: 12px;
                    left: rem(12px);
                    position: relative;
                    width: 32px;
                    width: rem(32px);
                    z-index: 1;
                }
            }

            a.header__action {
                @include item__action();
                bottom: 0;
                height: auto;
                right: 0px;
                top: 0;

                &:before {
                    font-size: 24px;
                    left: 4px;
                    position: relative;
                    width: 32px;
                    z-index: 1;
                }

                .iconUxt {
                    font-size: 24px;
                    left: 12px;
                    position: relative;
                    z-index: 1;
                }
            }

            a.header__action + a.header__action {
                right: 52px;
                width: 48px;

                &:after {
                    right: 8px;
                }
            }

            a.header__action + a.header__action + a.header__action {
                right: 100px;
            }
        }
    }

    // one single list (group of list items)
    .listNavigation__list {
        background-color: inherit;
        list-style-type: none;
        margin: 0;
        padding-left: 0;
        overflow-y: auto;

        & + .listNavigation__header {
            border-top: none;
        }

        .list__item {
            border-bottom: 1px solid rgba(var(--color-border), 0.6);
            margin: 0;
            position: relative;

            &:last-child {
                border-bottom: 1px solid rgba(var(--color-border), 1);
            }

            .item__link {
                @include item__link();

                .item__icon {
                    align-items: center;
                    display: flex;
                    flex-shrink: 0;
                    font-size: 24px;
                    margin-left: 16px;
                    pointer-events: none;
                    // IE11 does not bubble the active state hence we set it to none and then activate it again for all browsers except IE11
                    // otherwise inside the item no clickable elements (e.g. buttons) would be possible
                    @supports (outline-offset: -2px) {
                        pointer-events: auto;
                    }
                }

                .item__content {
                    flex-grow: 1;
                    margin-left: 16px;
                    padding-top: 12px;
                    padding-bottom: 12px;
                    pointer-events: none;
                    // IE11 does not bubble the active state hence we set it to none and then activate it again for all browsers except IE11
                    // otherwise inside the item no clickable elements (e.g. buttons) would be possible
                    @supports (outline-offset: -2px) {
                        pointer-events: auto;
                    }

                    .label, h4 {
                        font-family: $defaultFont;
                        font-size: 14px;
                        font-size: rem(14px);
                        line-height: 20px;
                        line-height: rem(20px);
                        margin-bottom: 2px;
                        margin-top: 0;
                    }

                    .description {
                        color: rgba(var(--color-font-secondary), 1);
                        font-size: 12px;
                        font-size: rem(12px);
                        line-height: 16px;
                        line-height: rem(16px);
                    }
                }

                .item__metaInfo {
                    align-items: center;
                    display: flex;
                    flex-shrink: 0;
                    font-size: 24px;
                    padding-left: 16px;
                    padding-right: 16px;
                    pointer-events: none;
                    // IE11 does not bubble the active state hence we set it to none and then activate it again for all browsers except IE11
                    // otherwise inside the item no clickable elements (e.g. buttons) would be possible
                    @supports (outline-offset: -2px) {
                        pointer-events: auto;
                    }
                }

                .item__metaInfo + .item__metaInfo {
                    padding-left: 8px;
                }

            }

            &.list__item--drilldown {
                background-color: inherit;


                .item__metaInfo {
                    padding-right: 16px;
                }

                .item__metaInfo:last-child {
                    padding-right: 64px;
                }

                &:hover {
                    .item__link .label {
                        text-decoration: underline;
                    }
                }
            }

            &.list__item--oneLine {
                .description {
                    display: none;
                }
            }

            &.list__item--twoLine,
            &.list__item--threeLine {
                .description {
                    height: 16px;
                    height: rem(16px);
                    overflow: hidden;
                    position: relative;

                }
            }

            &.list__item--threeLine {
                .description {
                    height: 32px;
                    height: rem(32px);
                }
            }

            &.list__item--twoLine,
            &.list__item--threeLine {

                &.has-overflowIndicator {
                    .description {
                        padding-right: 1em;
                        word-break: break-all;
                    }
                    .description:after {
                        bottom: 0;
                        content: ' ';
                        font-size: 12px;
                        height: 16px;
                        height: rem(16px);
                        position: absolute;
                        right: 0;
                        top: auto;
                    }
                }
            }

            .item__drilldown {
                @include item__action();
                @include iconUxt-regular(arrowSingleRight);
                right: 0px;
                top: 0;
                bottom: 0;
                height: auto;

                &:before {
                    position: relative;
                    left: 8px;
                    font-size: 24px;
                    width: 32px;
                    z-index: 1;
                }
            }
        }
    }

    // alternative coloring for lists
    &--alternateBgColor {
        background-color: rgba(var(--color-base1000), 1);
        .listNavigation__header {
            &.is-advanced {
                a.header__back {
                    &:after {
                        background-color: rgba(var(--color-base1000), 1);
                    }
                }
            }
        }
        .listNavigation__list {
            .list__item {
                .item__drilldown {
                    &:after {
                        background-color: rgba(var(--color-base1000), 1);
                    }
                }
            }
        }
    }
}
