.menu {
  display: inline-block;
  position: relative;
  z-index: 1;

  > .button,
  > button {
    background-color: rgba(var(--color-interface), 1);
    box-sizing: border-box;
    font-size: 24px;
    height: 32px;
    width: 32px;
    @include iconUxt-filled(more);
  }

  .menu__items {
    background-color: rgba(var(--color-interface), 1);
    border: 1px solid rgba(var(--color-border), 1);
    border-radius: 3px;
    box-shadow: 0 2px 30px 0 rgba(var(--color-shadow), 0.1);
    box-sizing: border-box;
    display: none;
    margin-top: -6px;
    max-height: 450px;
    max-width: 320px;
    overflow-y: auto;
    padding: 8px 0px;
    position: absolute;

    .menu__item {
      box-sizing: border-box;
      display: block;
      font-size: 14px;
      font-size: rem(14px);
      height: 36px;
      line-height: 36px;
      padding: 0 30px 0 20px;
      max-height: 36px;
      max-width: 320px;
      overflow: hidden;
      text-decoration: none;
      text-overflow: ellipsis;
      white-space: nowrap;

      @include hoverTransition($animate: (background-color, color, border));

      .iconUxt {
        margin: 0 32px 0 0;
        // position: relative; /* Menu items' icons were misaligned in firefox */
        vertical-align: middle;

        &:before {
          font-size: 24px;
          position: absolute;
        }
      }

      &:hover {
        background-color: rgba(var(--color-base900), 1);
        color: rgba(var(--color-primary-light), 1);
      }

      &:focus {
        box-shadow: inset 0 0 0 2px rgba(var(--color-primary-light), 1);
        // IE11 does not support outline-offset, hence the feature query
        outline: 0;
        @supports (outline-offset: -2px) {
          box-shadow: none;
          outline-offset: -2px;
          outline: 2px solid rgba(var(--color-primary-light), 1);
        }
      }

      &:active,
      &.is-pressed {
        background-color: rgba(var(--color-base800), 1);
      }

      &.is-disabled {
        color: rgba(var(--color-font-disabled), 1);
        &:hover {
          cursor: not-allowed;
        }
      }
    }
  }

  &.is-expanded {
    z-index: 2;

    > .button,
    > button {
      background-color: rgba(var(--color-primary-lighter), 1);
      border: 1px solid rgba(var(--color-primary), 1);
    }
    .menu__items {
      display: block;
    }
  }

  &.is-right-aligned {
    text-align: right;

    .menu__items {
      text-align: left;
      right: 0;
    }
  }

  &.is-bottom-aligned {
    .menu__items {
      bottom: 0;
      margin-bottom: 42px;
    }
  }
}

// if menu is preceeded by a button, add a spacing on the left
button,
.button {
    + .menu {
        margin-left: 8px;
        // if menu is preceeded by a button, add a spacing on the left
    }
}
