@mixin defaultTextStyles {
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
    //font-family: $defaultFont;
    font-weight: normal;
    text-rendering: optimizeLegibility;
}

@mixin headline1 {
    font-family: $defaultFont;
    font-size: 36px;
    font-size: rem(36px);
    font-weight: 900;
    line-height: 43px;
    line-height: rem(43px);
    margin-bottom: 20px;
    margin-bottom: rem(20px);
    margin-top: 0;
}

@mixin headline2 {
    font-family: $defaultFont;
    font-size: 31px;
    font-size: rem(31px);
    font-weight: bold;
    line-height: 43px;
    line-height: rem(43px);
    margin-bottom: 8px;
    margin-bottom: rem(8px);
    margin-top: 0;
}

@mixin headline3 {
    font-family: $defaultFont;
    font-size: 26px;
    font-size: rem(26px);
    font-weight: normal;
    line-height: 36px;
    line-height: rem(36px);
    margin-bottom: 8px;
    margin-bottom: rem(8px);
    margin-top: 0;
}

@mixin headline4 {
    font-family: $defaultFont;
    font-size: 21px;
    font-size: rem(21px);
    font-weight: normal;
    line-height: 29px;
    line-height: rem(29px);
    margin-bottom: 4px;
    margin-bottom: rem(4px);
    margin-top: 0;
}

@mixin headline5 {
    font-family: $defaultFont;
    font-size: 17px;
    font-size: rem(17px);
    font-weight: normal;
    line-height: 23px;
    line-height: rem(23px);
    margin-bottom: 4px;
    margin-bottom: rem(4px);
    margin-top: 0;
}

@mixin headline6 {
    font-family: $defaultFont;
    font-size: 16px;
    font-size: rem(16px);
    font-weight: normal;
    line-height: 22px;
    line-height: rem(22px);
    margin-bottom: 4px;
    margin-bottom: rem(4px);
    margin-top: 0;
}

@mixin paragraph {
    font-family: $defaultFont;
    line-height: 1.43em;
    margin-bottom: 16px;
    margin-bottom: rem(16px);
    margin-top: 0;

        &.p--small {
            font-size: 0.86em;
            line-height: 1.33em;
        }
}

@mixin small {
    font-size: 0.86em;
}

@mixin em {
    // special font face that is already italic is used
    font-family: $defaultFont;
    font-style: italic;
}

@mixin boldem {
    font-family: $defaultFont;
    font-style: italic;
    font-weight: bold;
}

@mixin blackem {
    font-family: $defaultFont;
    font-style: italic;
    font-weight: 900;
}

@mixin mark {
    font-family: $defaultFont;
    background-color: rgba(var(--color-primary-lighter), 1);
    color: rgba(var(--color-base000), 1);
}

@mixin strong {
    font-family: $defaultFont;
    font-weight: bold;
}

@mixin hasMargin($margin) {
    margin: $margin;
}

.h1 {
    @include defaultTextStyles;
    @include headline1;
}

.h2 {
    @include defaultTextStyles;
    @include headline2;
}

.h3 {
    @include defaultTextStyles;
    @include headline3;
}

.h4 {
    @include defaultTextStyles;
    @include headline4;
}

.h5 {
    @include defaultTextStyles;
    @include headline5;
}

.h6 {
    @include defaultTextStyles;
    @include headline6;
}

.h1, .h2, .h3, .h4, .h5, .h6 {
    &.has-margin-0 {
        @include hasMargin(0);
    }
}

.subheadline {
    @include defaultTextStyles;
    color: rgba(var(--color-font-secondary), 1);
    font-size: 17px;
    font-size: rem(17px);
    line-height: 25px;
    line-height: rem(25px);
}

.p {
    @include defaultTextStyles;
    @include paragraph;
}

.b,
.strong {
    @include defaultTextStyles;
    @include strong;
}

.small {
    @include defaultTextStyles;
    @include small;
}

.i,
.em {
    @include defaultTextStyles;
    @include em;
}

.i .b,
.b .i,
.strong .i,
.i .strong,
.em .b,
.b .em,
.strong .em,
.em .strong {
    @include boldem;
}

.h1 {
    .em, .i {
        @include blackem;
    }
}

.h2 {
    .em, .i {
        @include boldem;
    }
}

.mark {
    @include defaultTextStyles;
    @include mark;
}

.code,
.monospace {
    font-family: $monospacedFont;
    font-size: 0.8125em; // default monospace font size for code elements by user agent stylesheet
}


.a {
    font-family: $defaultFont;
    @include hoverTransition($animate: (color));

    &, &:visited {
        color: rgba(var(--color-primary), 1);
        text-decoration: underline;
    }

    &:hover {
        text-decoration: underline;
        color: rgba(var(--color-primary-light), 1);
    }

    &:focus-visible {
        color: rgba(var(--color-primary-light), 1);
        outline: 2px solid rgba(var(--color-primary-light), 1);
        text-decoration: underline;
    }

    &:active {
        color: rgba(var(--color-primary-dark), 1);
    }

    &.is-external {
        //&[href^="//"],
        //&[href^="http://"],
        //&[href^="https://"] {
        @include iconUxt-regular(linkArrow, after);
        padding-right: 10px;
        padding-right: rem(10px);
        position: relative;

        &:after {
            font-size: 24px;
            font-size: rem(24px);
            height: 24px;
            height: rem(24px);
            position: absolute;
            bottom: 0.01em;
            width: 10px;
            width: rem(10px);
            text-decoration: underline;
            display: inline-block;
        }

        // second declaration is necessary for IE 11
        &:after {
            text-decoration: none;
        }
    }
}


//removeIf(sassHandover)
// style native elements when forced defaults
&.#{$forceDefaultClass} {
    & {
        @include defaultTextStyles;
    }

    h1 {
        @include defaultTextStyles;
        @include headline1;
    }

    h2 {
        @include defaultTextStyles;
        @include headline2;
    }

    h3 {
        @include defaultTextStyles;
        @include headline3;
    }

    h4 {
        @include defaultTextStyles;
        @include headline4;
    }

    h5 {
        @include defaultTextStyles;
        @include headline5;
    }

    h6 {
        @include defaultTextStyles;
        @include headline6;
    }

    h1, h2, h3, h4, h5, h6 {
        &.has-margin-0 {
            @include hasMargin(0);
        }
    }

    p {
        @include defaultTextStyles;
        @include paragraph;
    }

    b, strong {
        @include defaultTextStyles;
        @include strong;
    }

    small {
        @include defaultTextStyles;
        @include small;
    }

    i, em {
        @include defaultTextStyles;
        @include em;
    }

    i b,
    b i,
    strong i,
    i strong,
    em b,
    b em,
    strong em,
    em strong {
        @include boldem;
    }

    h1 {
        em, i {
            @include blackem;
        }
    }

    h2 {
        em, i {
            @include boldem;
        }
    }

    mark {
        @include defaultTextStyles;
        @include mark;
    }

    code {
        @extend .code;
    }

    a {
        @extend .a;
    }


    hr {
        border-top: 1px solid rgba(var(--color-base800), 1);
    }

    ul, ol {
        @include defaultTextStyles;
        line-height: 21px;
        line-height: rem(21px);
        margin-block-start: 1em;
        margin-block-end: 1em;
        padding-left: 22px;
        padding-left: rem(22px);

        ul,
        ol {
            padding-left: 32px;
            padding-left: rem(32px);
            margin-top: 12px;
            margin-top: rem(12px);
            margin-bottom: 12px;
            margin-bottom: rem(12px);
        }

        li {
            margin: 0 0 12px;
            margin: rem(0 0 12px);
        }
    }

    dl {
        @include defaultTextStyles;
        margin: 0;

        dt {
            font-family: $defaultFont;
            font-size: 17px;
            font-size: rem(17px);
            font-weight: bold;
            line-height: 23px;
            line-height: rem(23px);
            margin: 0;
            padding-bottom: 12px;
            padding-bottom: rem(12px);
        }

        dd {
            font-family: $defaultFont;
            font-size: 14px;
            font-size: rem(14px);
            line-height: 20px;
            line-height: rem(20px);
            margin: 0;
            padding-bottom: 32px;
            padding-bottom: rem(32px);
        }

        &.dl--inlineList {
            dt {
                float: left;
                width: 33%;
            }

            dd {
                margin: 0 0 0 35%;
                padding: 0;

                + dd {
                    padding: 12px 0 0 0;
                    padding: rem(12px 0 0 0);
                }

                &:last-child {
                    padding-bottom: 32px;
                    padding-bottom: rem(32px);
                }
            }

            dd + dt {
                padding-top: 32px;
                padding-top: rem(32px);

                + dd {
                    padding-top: 32px;
                    padding-top: rem(32px);
                }
            }
        }
    }
}
//endRemoveIf(sassHandover)
